@charset "UTF-8";
body {
  font-family: "Lora-Bold";
}

.container__form-img {
  max-width: 905px;
  position: relative;
  padding-top: 270px;
  padding-bottom: 100px;
  min-height: 100vh;
}
.container__form-img h1 {
  font-family: "Lora-Bold";
  font-size: 56px;
  font-weight: 600;
  color: #215C75;
  z-index: 1;
  position: relative;
}
@media screen and (max-width: 768px) {
  .container__form-img h1 {
    font-size: 20px;
  }
}
.container__form-img form::after {
  position: absolute;
  left: 0;
  content: "";
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
@media screen and (max-width: 870px) {
  .container__form-img form::after {
    top: -256px;
    left: 0;
    width: 100%;
    height: 225px;
    background-size: contain;
  }
}
.container__form-img--left form::after {
  top: 127px;
  left: -155px;
  width: 269px;
  height: 368px;
  background-image: url("/src/images/artboard.svg");
}
@media screen and (max-width: 1100px) {
  .container__form-img--left form::after {
    top: -303px;
    left: calc(50% - 148px);
  }
}
.container__form-img--center form::after {
  top: -13px;
  left: calc(50% - 164px);
  width: 269px;
  height: 368px;
  background-image: url("/src/images/artboard-center.svg");
}
@media screen and (max-width: 1300px) {
  .container__form-img--center form::after {
    top: -340px;
    left: calc(50% - 148px);
  }
}
.container__form-img--right form::after {
  top: 168px;
  left: auto;
  right: 0;
  width: 259px;
  height: 418px;
  background-image: url("/src/images/artboard-right.svg");
}
@media screen and (max-width: 1300px) {
  .container__form-img--right form::after {
    top: -280px;
    left: calc(50% - 148px);
  }
}

.form-img,
.form-img > * {
  width: 100%;
  margin: auto;
}

.form-img .error {
  color: #bf1650;
}
.form-img .error::before {
  display: inline;
  content: "âš  ";
}
.form-img__row {
  font-family: "Open-sans";
  font-size: 21px;
  font-weight: bold;
  display: flex;
  margin-bottom: 20px;
  justify-content: space-between;
}
.form-img__remember-me {
  display: flex;
  white-space: nowrap;
}
.form-img__remember-me input[type=checkbox] {
  padding: 0;
  margin: 0;
  margin-right: 10px;
  width: 27px;
  height: 27px;
}
.form-img input {
  display: block;
  box-sizing: border-box;
  width: 100%;
  border-radius: 4px;
  border: 1px solid white;
  padding: 20px 15px;
  margin-bottom: 30px;
  font-size: 14px;
}
.form-img .input[type=submit] {
  width: 98px;
  height: 98px;
  border: none !important;
  margin-top: 40px;
  background-color: transparent;
  position: relative;
  cursor: pointer;
  background-image: url("/src/images/btn.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.form-img .input[type=submit]::after {
  position: absolute;
  top: 0px;
  left: 0px;
  content: "";
  width: 98px;
  height: 98px;
  background-image: url("/src/images/artboard.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.form-img input[type=submit]:hover {
  border: none;
  background-color: transparent;
}
.form-img input[type=submit]:active {
  transition: 0.3s all;
  transform: translateY(3px);
  border: 1px solid transparent;
  opacity: 0.8;
}
.form-img input:disabled {
  opacity: 0.4;
}

.gallery {
  display: grid;
  gap: 12px;
  margin-bottom: 10px;
}
.gallery__item {
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media screen and (min-width: 768px) {
  .gallery {
    grid-template-columns: repeat(2, calc(50% - 8px));
    gap: 16px;
  }
}
.register__signin {
  font-family: "Open-sans";
  font-weight: bold;
  color: #403F3F;
  font-size: 30px;
}

.register__login-link {
  color: #215C75;
  text-decoration: none;
}